import React, { useState, useEffect } from "react";
import { getCategories, list } from "./apiCore";
import Card from "./Card";

const Search = () => {
    const [data, setData] = useState({
        categories: [],
        category: "",
        search: "",
        results: [],
        searched: false
    });

    const { categories, category, search, results, searched } = data;

    const loadCategories = () => {
        getCategories().then(data => {
            if (data?data.error:false) {
                console.log(data.error);
            } else {
                setData({ ...data, categories: data });
            }
        });
    };

    useEffect(() => {
        loadCategories();
    }, []);

    const searchData = () => {
        // console.log(search, category);
        if (search) {
            list({ search: search || undefined, category: category }).then(
                response => {
                    if (response.error) {
                        console.log(response.error);
                    } else {
                        setData({ ...data, results: response, searched: true });
                    }
                }
            );
        }
    };

    const searchSubmit = e => {
        e.preventDefault();
        searchData();
    };

    const handleChange = name => event => {
        setData({ ...data, [name]: event.target.value, searched: false });
    };

    const searchMessage = (searched, results) => {
        if (searched && results.length > 0) {
            return `Found ${results.length} products`;
        }
        if (searched && results.length < 1) {
            return `No products found`;
        }
    };

    const searchedProducts = (results = []) => {
        return (
            <div>
                <h2 className="mt-4 mb-4">
                    {searchMessage(searched, results)}
                </h2>

                <div className="row">
                    {results.map((product, i) => (
                        <div className="col-md-3 col-sm-6 col-xs-8 offset-xs-2 mb-3">
                            {/* <Card key={i} product={product} /> */}
                            <Card product={{ ...product, "description": [product.description.substring(0, 100) + "..."] }} />

                        </div>
                    ))}
                </div>
            </div>
        );
    };

    const searchForm = () => (
        <div id="search" >
        <form onSubmit={searchSubmit}>
            <span className="input-group-text">
                <div className="input-group input-group-lg">
                    <div className="input-group-prepend" id="catogory-select-parent">
                        <select
                            className="btn mr-2"
                            onChange={handleChange("category")}
                            id="category-select"
                        >
                            <option value="All" id="option-search">All</option>
                            
                            {categories?categories.map((c, i) => (
                                <option key={i} value={c._id}>
                                    {c.name}
                                </option>
                            )):<></>}
                        </select>
                    </div>
                   
                    <input
                        type="search"
                        className="form-control"
                        id="search-by-name"
                        onChange={handleChange("search")}
                        placeholder="Search by name"
                    />
                  
                </div>
                <div
                    className="input-group-append ml-2"
                    style={{ border: "none" }}
                >
                    <button className="input-group-text" id="search-button">Search</button>
                </div>
            </span>
        </form>
        </div>
    );

    return (
        <div className="row">
            <div className="container mb-3">{searchForm()}</div>
            <div className="container-fluid mb-3">
                {searchedProducts(results)}
            </div>
        </div>
    );
};

export default Search;
